export type SocketDataType = string | Buffer | ArrayBuffer | Buffer[] | object;

export type DefaultTransferBuffer = void;
export interface ISocket<TTransferBuffer = DefaultTransferBuffer> {
    Send(packet: string | object, options?: { transferBuffers?: Array<TTransferBuffer> }): void;
    OnMessage?: (data: SocketDataType) => void;
    OnMessageWithDirectObject?: boolean;
    destroy(): void;
}